.homePage {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
}

.homePage .homePage-text {
  color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.homePage .homePage-text .first-container,
.homePage .homePage-text .second-container,
.homePage .homePage-text .third-container {
  cursor: pointer;
}

.homePage .homePage-text .first-container h1,
.homePage .homePage-text .second-container h1,
.homePage .homePage-text .third-container h1 {
  letter-spacing: 2px;
  font-size: 4.2rem;
  text-align: center;
}

.homePage span {
  text-transform: uppercase;
  position: relative;
}

.homePage span:after {
  content: "";
  position: absolute;
  height: 100%;
  width: 0;
  top: 0;
  left: 0;
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
  z-index: -5;
}

.homePage .share:hover > h1 > span:nth-child(1):after,
.homePage .share:hover > h1 > span:nth-child(2):after,
.homePage .share:hover > h1 > span:nth-child(3):after,
.homePage .share:hover > h1 > span:nth-child(4):after,
.homePage .share:hover > h1 > span:nth-child(5):after,
.homePage .share:hover > h1 > span:nth-child(6):after,
.homePage .share:hover > h1 > span:nth-child(7):after,
.homePage .share:hover > h1 > span:nth-child(8):after,
.homePage .share:hover > h1 > span:nth-child(9):after,
.homePage .share:hover > h1 > span:nth-child(10):after,
.homePage .share:hover > h1 > span:nth-child(11):after,
.homePage .share:hover > h1 > span:nth-child(12):after,
.homePage .share:hover > h1 > span:nth-child(13):after,
.homePage .share:hover > h1 > span:nth-child(14):after,
.homePage .share:hover > h1 > span:nth-child(15):after,
.homePage .share:hover > h1 > span:nth-child(16):after,
.homePage .share:hover > h1 > span:nth-child(17):after,
.homePage .share:hover > h1 > span:nth-child(18):after{
  background: white;
  width: 100%;
  -webkit-transition: 0.6s;
  -o-transition: 0.6s;
  transition: 0.6s;
}

.homePage .share:hover > h1 > span:nth-child(1):after {
  -webkit-transition-delay: 100ms;
       -o-transition-delay: 100ms;
          transition-delay: 100ms;
  -webkit-transform: rotate(-25deg);
      -ms-transform: rotate(-25deg);
          transform: rotate(-25deg);
}

.homePage .share:hover > h1 > span:nth-child(1),
.homePage .share:hover > h1 > span:nth-child(2),
.homePage .share:hover > h1 > span:nth-child(3),
.homePage .share:hover > h1 > span:nth-child(4),
.homePage .share:hover > h1 > span:nth-child(5),
.homePage .share:hover > h1 > span:nth-child(6),
.homePage .share:hover > h1 > span:nth-child(7),
.homePage .share:hover > h1 > span:nth-child(8),
.homePage .share:hover > h1 > span:nth-child(9),
.homePage .share:hover > h1 > span:nth-child(10),
.homePage .share:hover > h1 > span:nth-child(11),
.homePage .share:hover > h1 > span:nth-child(12),
.homePage .share:hover > h1 > span:nth-child(13),
.homePage .share:hover > h1 > span:nth-child(14),
.homePage .share:hover > h1 > span:nth-child(15),
.homePage .share:hover > h1 > span:nth-child(16),
.homePage .share:hover > h1 > span:nth-child(17),
.homePage .share:hover > h1 > span:nth-child(18) {
  color: #39393B;
}

.homePage .share:hover > h1 > span:nth-child(1) {
  -webkit-transition-delay: 110ms;
       -o-transition-delay: 110ms;
          transition-delay: 110ms;
}



