.Status {
  width: 100%;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.Status .left {
  width: 25%;
  margin: 10px;
}

.Status .right {
  width: 75%;
  margin: 10px;
}

.Status .cv {
  width: 100%;
  height: auto;
  display: block;
  margin: auto;
  -webkit-filter:grayscale(100%);
  -moz-filter:grayscale(100%);
  -o-filter:grayscale(100%);
  -ms-filter:grayscale(100%);
  filter:grayscale(100%);
}

.Status button {
  background-color: #02BEC4;
  color: #fff;
  font-family: 'Rock Salt', cursive;
  font-weight: bold;
  border: none;
  width: 100%;
  height: 70px;
  text-align: left;
  padding-left: 20px;
  -webkit-outline: none;
  -moz-outline: none;
  -o-outline: none;
  -ms-outline: none;
  outline: none;
  letter-spacing: 5px;
  margin-bottom: 5px;
  cursor: pointer;
  -webkit-transition: background-color 1s linear;
  -o-transition: background-color 1s linear;
  transition: background-color 1s linear;
  border-radius: 5px;
}

/* removing firefox dotted outline*/
button::-moz-focus-inner {
  border: 0;
}

.inactive {
  outline: none;
}

.Status button.active {
  display: block;
  background-color: #060F4F;
}

.Status button:after {
  content: '+';
  color: #fff;
  font-size: 15px;
  float: right;
  margin-right: 20px;
  font-family: 'Rock Salt', cursive;
}

.Status .active:after {
  content: '-';
}

.Status .panel {
  padding: 20px;
  background-color: rgb(242, 242, 242, .3);
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.Status .panel:before {
  content: '';
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid rgb(242, 242, 242, .3);
  border-left: 8px solid transparent;
  position: relative;
  top: -48px;
  right: -40px;
}

.Status .panel p {
  padding-bottom: 20px;
  letter-spacing: 2px;
  color: #333333;
}

.Status .panel p span {
  display: block;
  padding-bottom: 2px;
  line-height: 1.3rem;
}

.Status .linkedin {
  text-decoration: none;
  color: #060F4F;
}

.Status .pdf {
  color: #02BEC4;
  border: none;
  padding: 2px;
  background-color: rgba(6, 15, 79, 0.7);
  text-decoration: none;
  -webkit-transition: color 1s linear, background-color 2s linear;
  -o-transition: color 1s linear, background-color 2s linear;
  transition: color 1s linear, background-color 2s linear;
}

.Status .pdf:hover {
  text-decoration: underline;
  color: rgba(6, 15, 79, 0.7);
  background-color: rgba(2, 190, 196, 0.7);
}

@media (max-width: 600px) {
  .Status {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  .Status .left,
  .Status .right {
    width: 80%;
  }

}



