.Navigation {
  background: #060F4F;
  padding: 15px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
}

.Navigation a.logo {
  text-decoration: none;
  color: #ffffff;
  font-size: 2rem;
  font-weight: bold;
}

.Navigation a.logo:hover {
  color: #A9E8DC;
}

.Navigation .link {
  position: absolute;
  right: 50px;
}

.Navigation .link a {
  /*margin: 25px 10px;*/
  font-weight: bold;
  color: #E1F7E7;
  text-decoration: none;
  font-size: 1.2rem;
  margin: 15px 10px;
}

.Navigation .link a:hover {
  text-decoration: underline;
}

/* .Navigation .link > a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
} */

.Navigation .link a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 2;
  left: 0;
  background-color: #E1F7E7;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  -ms-transform: scaleX(0);
      transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  -o-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.Navigation h1 {
/*  margin-left: 10px;
  margin-bottom: 10px;*/
  color: #E1F7E7;
  font-size: 2rem;
}

.Navigation h1:hover {
  color: #A9E8DC;
}

/*********** menu bar *********/
.Navigation #input {
  display: none;
}

.Navigation .menuBar {
  width: 40px;
  height: 40px;
  padding-bottom: 25px;
  margin: auto auto auto 0;
  position: relative;
  cursor: pointer;
  display: none;
}

.Navigation .menuBar .bar {
  width: 30px;
  background: black;
  height: 3px;
  display: inline-block;
  position: absolute;
  left: 5px;
  -webkit-transition: all 0.15s ease-in-out;
  -o-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  opacity: 1;
}

#input:checked ~ .menuBar .bar {
  width: 25px;
  background: #02BEC4;
}

.menuBar .bar:nth-child(1) {
  top: 12px;
  background: #0284A8;
}

.menuBar .bar:nth-child(2) {
  top: 20px;
  background: #0284A8;
}

.menuBar .bar:nth-child(3) {
  top: 28px;
  background: #0284A8;
}

#input:checked ~ .menuBar .bar:nth-child(1) {
  -webkit-transform: rotate(135deg) translateY(-4px) translateX(4px);
      -ms-transform: rotate(135deg) translateY(-4px) translateX(4px);
          transform: rotate(135deg) translateY(-4px) translateX(4px);
}

#input:checked ~ .menuBar .bar:nth-child(2) {
  left: -5px;
  opacity: 0;
}

#input:checked ~ .menuBar .bar:nth-child(3) {
  -webkit-transform: rotate(-135deg) translateY(7px) translateX(7px);
      -ms-transform: rotate(-135deg) translateY(7px) translateX(7px);
          transform: rotate(-135deg) translateY(7px) translateX(7px);
}

/*********** mobile version ***********/
@media (min-width: 601px) and (max-width: 900px) {
  .Navigation {
    width: 100%;
    overflow: hidden;
    position: relative;
  }
  .Navigation h1 {
    margin: 0;
  }
  .Navigation .menuBar {
    display: inline-block;
  }
  .Navigation .link {
    background-color: #0284A8;
    position: absolute;
    width: 100%;
    left: 100%;
    text-align: center;
  }
  #input:checked ~ .link {
    left: 0;
  }
  .list a {
    width: 100%;
    display: block;
    color: #060F4F;
  }
}

@media (max-width: 600px) {
  .Navigation {
    width: 100%;
    overflow: hidden;
    position: relative;
  }
  .Navigation h1 {
    margin: 0;
  }
  .Navigation .menuBar {
    display: inline-block;
  }
  .Navigation .link {
    background-color: #0284A8;
    position: absolute;
    width: 100%;
    left: 100%;
    text-align: center;
  }
  #input:checked ~ .link {
    left: 0;
  }
  .list a {
    width: 100%;
    display: block;
    color: #060F4F;
  }
}



